.header-header {
  display: flex;
  flex-direction: column;
  height: 500px;
  background-image: url('../images/bg-hero.jpg');
  background-blend-mode: normal;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  @media screen and (min-width: $lg) {
    height: 629px;
  }
  @media screen and (min-width: $xl) {
    height: 838px;
  }
}
.name-company {
  h1 {
    font-size: 22px;
    padding-bottom: 16px;
    padding-top: 16px;
    @media screen and (min-width: $md) {
      height: 24px;
    }
    @media screen and (min-width: $lg) {
      height: 26px;
    }
    @media screen and (min-width: $xl) {
      height: 29px;
    }
  }
}
.logo {
  width: 120px;
  margin: auto;
}
.serves {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 2;
  width: 100%;
  h2 {
    font-size: 48px;
    text-align: center;
    max-width: 100%;
    width: 900px;
    @extend %playfair-display;
  }
}
.name {
  font-size: 29px;
  text-transform: uppercase;
  font-weight: 700;
  @extend %montserrat;
}
