@import '~node_modules/modern-normalize/modern-normalize.css';
html,
body {
  scroll-behavior: smooth;
}
body {
  font-size: 20px;
  font-style: normal;
  letter-spacing: 1;
  background-color: $bgCommon;
  color: $colorMainText;
  text-align: center;
  @extend %montserrat;
}
svg {
  pointer-events: none;
}

button {
  cursor: pointer;
  border: none;
}
a {
  text-decoration: none;
}
.hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

img {
  display: block;
  height: auto;
}
