.subscribe {
  padding-bottom: 56px;
  padding-top: 56px;
}

.subscribe-title {
  @extend %playfair-display;
  font-size: 48px;
  font-weight: 400;
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 32px;
}
.subscribe-explain {
  @extend %source-sans-3;
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 16px;
  color: $colorTextSubscribeExplain;
  @media screen and (min-width: $xl) {
    font-size: 18px;
  }
}
.subscribe-input {
  @extend %source-sans-3;
  font-size: 16px;
  color: $colorMainText;
  border: 1px solid $bgInviteMessage;
  border-radius: 4px;
  outline: none;
  padding-bottom: 7px;
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 23px;
  font-size: 16px;
  background: transparent;
  @media screen and (min-width: $xl) {
    font-size: 18px;
  }
}
.subscribe-submit {
  @extend %source-sans-3;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 4px;
  outline: none;
  &:active {
    background: $bgInviteMessage;
  }
}
.subscribe-form {
  position: relative;
  max-width: 100%;
  width: 768px;
  margin: auto;
  display: grid;
  grid-template-columns: 5fr 1fr;
  gap: 24px;
}
.subscribe-error {
  display: none;
  &.is-error {
    position: absolute;
    bottom: -20px;
    left: 0;
    display: block;
    color: $colorErrorText;
    text-transform: initial;
    font-size: 16px;
    @extend %source-sans-3;
    @media screen and (min-width: $xl) {
      font-size: 18px;
    }
  }
}

.portfolio {
}
.message {
  display: none;
  &.is-open {
    display: flex;
    justify-content: end;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $bgBackdrop;
    overflow-y: auto;
  }
  .container {
    opacity: 0;
    &.is-open {
      width: 100%;
      max-width: 380px;
      border-radius: 4px;
      background: $bgSecond;
      border: 2px solid $bgSecond;
      margin-bottom: 24px;
      transition: opacity 1000ms ease;
      opacity: 1;
      height: fit-content;
      @media screen and (min-width: $xs) {
        width: 380px;
      }
    }
  }
  @media screen and (min-width: $md) {
    transform: translateX(0);
    right: 24px;
    bottom: 24px;
    height: fit-content;
  }
}

.main-header {
  background: $bgTitleMessage;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 24px;
  padding-bottom: 48px;
}
.title-message {
  @extend %source-sans-3;
  font-size: 22px;
  color: $colorMainText;
}
.invite {
  padding-left: 24px;
  padding-right: 24px;
}
.text-invite {
  @extend %source-sans-3;
  font-size: 16px;
  line-height: 1.5;
  text-align: left;
  position: relative;
  border-radius: 4px;
  transform: translateY(-24px);
  color: $colorInviteMessage;
  background: $bgInviteMessage;
  padding-bottom: 12px;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 12px;
  &::before {
    content: '';
    bottom: -18px;
    left: 16px;
    position: absolute;
    width: 24px;
    height: 24px;
    background-image: url(../images/svg/slife.svg);
  }
}

.message-form {
  padding-left: 24px;
  padding-right: 24px;

  ul {
    display: flex;
    flex-direction: column;
    gap: 24px;
    margin-bottom: 24px;
  }
}
.input-list {
  position: relative;
}
.input-message {
  border: 1px solid $bgInviteMessage;
  border-radius: 4px;
  outline: none;
  padding-bottom: 7px;
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 23px;
  font-size: 16px;
  width: 100%;
}
label {
  position: relative;
  display: block;
}
.sign {
  transition: all 500ms ease;
  position: absolute;
  text-transform: initial;

  text-transform: initial;
  font-size: 16px;
  font-weight: 400;
  left: 16px;
  top: 50%;
  transform: translateY(-50%);
  &.sign-message {
    color: $colorInviteMessage;
  }
  &.sign-subscribe {
    color: $colorMainText;
  }
}
.input {
  &:focus ~ .sign {
    font-size: 12px;
    transform: translateY(-150%);
  }
}
.textarea {
  font-size: 16px;
  color: $colorInviteMessage;
  text-transform: initial;
  resize: vertical;
  min-height: 202px;
  &:placeholder {
    font-size: 16px;
    color: $colorInviteMessage;
  }
}

.name-error {
  display: none;
  &.is-error {
    display: block;
    color: $colorErrorText;
    text-transform: initial;
    font-size: 16px;
    position: absolute;
    bottom: 0;
    transform: translateY(20px);
    @extend %source-sans-3;
    @media screen and (min-width: $xl) {
      font-size: 18px;
    }
  }
}
.email-error {
  display: none;
  &.is-error {
    display: block;
    color: $colorErrorText;
    text-transform: initial;
    font-size: 16px;
    position: absolute;
    bottom: 0;
    transform: translateY(22px);
    @extend %source-sans-3;
    @media screen and (min-width: $xl) {
      font-size: 18px;
    }
  }
}
.text-error {
  display: none;
  &.is-error {
    display: block;
    color: $colorErrorText;
    text-transform: initial;
    font-size: 16px;
    position: absolute;
    bottom: 0;
    transform: translateY(22px);
    @extend %source-sans-3;
    @media screen and (min-width: $xl) {
      font-size: 18px;
    }
  }
}
.submit-message {
  @extend %source-sans-3;
  font-size: 14px;
  margin-top: 24px;
  margin-bottom: 24px;
  padding-bottom: 8px;
  padding-left: 32px;
  padding-right: 32px;
  padding-top: 8px;
  background: $bgCommon;
  border-radius: 4px;
  color: $colorMainText;
  text-transform: uppercase;
}
.message-button {
  position: fixed;
  bottom: 24px;
  right: 24px;
  border: none;
  display: flex;
  margin-left: auto;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  background: $bgTitleMessage;
  justify-content: center;
  align-items: center;
}
.toggle-button {
  fill: $colorMainText;
  width: 44px;
  height: 44px;
}
.open {
  opacity: 0;
  &.is-open {
    transition: opacity 1000ms ease;
    opacity: 1;
  }
}
.close {
  opacity: 0;
  &.is-open {
    transition: opacity 1000ms ease;
    opacity: 1;
  }
}
