// color
$bgCommon: rgb(22, 22, 22);
$bgSecond: #ffffff;
$bgTitleMessage: rgb(111, 111, 111);
$bgInviteMessage: #c0c0c0;
$bgBackdrop: rgba(0, 0, 0, 0.3);
$colorInviteMessage: rgb(48, 48, 48);
$colorTextSubscribeExplain: rgb(164, 164, 164);
$colorMainText: #ffffff;
$colorSecondText: rgb(22, 22, 22);
$colorErrorText: rgb(239, 0, 0);

// fonts
%source-sans-3 {
  font-family: 'Source Sans 3', sans-serif;
  font-optical-sizing: auto;
}

%montserrat {
  font-family: 'Montserrat', sans-serif;
  font-optical-sizing: auto;
}
%playfair-display {
  font-family: 'Playfair Display', serif;
  font-optical-sizing: auto;
}

// breakpoints
$xs: 380px;
$sm: 480px;
$md: 768px;
$lg: 1024px;
$xl: 1440px;
